@import url('https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,700;1,400&family=Libre+Franklin:ital,wght@0,400;0,500;1,300&family=Poppins:ital,wght@0,300;0,500;0,700;1,400;1,500&family=Roboto+Slab:wght@300;400;500;700&family=Ysabeau+SC:wght@400;500;600&display=swap');
*{
    box-sizing: border-box;
    padding: 0px;
    margin: 0px;
}
.orderDetails-container{
    width: 95%;
    margin: auto;
    /* height: 30vmax; */
    margin: 1vmax auto;

    display: flex;
    justify-content: center;
    align-items: flex-start;
    gap: 2vmax;
}
.left-orderDetails-container{
    width: 80%;
}
.right-orderDetails-container{
    width: 35%;
   
    height: auto;

}
.heading-orderDetails{
    background-color: #fff;

    box-shadow: rgba(17, 17, 26, 0.1) 0px 1px 0px, 
    rgba(17, 17, 26, 0.1) 0px 8px 24px, rgba(17, 17, 26, 0.1) 
    0px 16px 48px;
    margin: 0 0 1vmax 0;
}
.heading-orderDetails>h3{
font: 500 1.5vmax 'Roboto Slab', serif;
color: rgba(0, 0, 0, 0.7);
padding: 1.5vmax 1vmax;
}
.heading-orderDetails>p{
    font: 500 1vmax  'Poppins', sans-serif;;
color: rgba(0, 0, 0, 0.9);
padding: 0vmax 0 0.5vmax 1vmax;
}
.orderDetails-address{
    box-shadow: rgba(17, 17, 26, 0.1) 0px 1px 0px, 
    rgba(17, 17, 26, 0.1) 0px 8px 24px, rgba(17, 17, 26, 0.1) 
    0px 16px 48px;
    padding: 1vmax;
    text-transform: uppercase;
    color: #2874f0;
    background-color: #fff;
    font-size: 14px;
    border-radius: 2px 2px 0 0;
    margin-bottom: 1vmax;
    
}
.address-text-orderDetails{
    background-color: white;
    display: grid;
    grid-template-columns: 2fr 2fr 2fr 2fr;
    justify-items: center;
    align-items: start;
}
.help>h3,.orderDetails-payment-status>h3,.orderDetails-status>h3{
    padding-top: 0vmax;
    font: 600 1.4vmax 'Ysabeau SC', sans-serif;
    text-transform: uppercase;
    color: rgba(0, 0, 0, 1);
    
}
.help>p,.orderDetails-payment-status>p,.orderDetails-status>p{
    font: 500 1.1vmax 'Poppins', sans-serif;;
    padding: 0.5vmax 0 0 0;
}



.address-delivery>h3{
    font: 600 1.4vmax 'Ysabeau SC', sans-serif;
    padding: 0 0 0 1vmax;
    text-transform: uppercase;
    color: rgba(0, 0, 0, 1);
}
.address-delivery>h4{
    font:500 1.2vmax 'Libre Franklin', sans-serif;
    padding: 0.5vmax 1vmax;
}
.address-delivery>h4>span{
    font:600 1.2vmax 'Libre Franklin', sans-serif;
    color: rgba(0, 0, 0, 0.8); 
    letter-spacing: 1px; 
}
.address-delivery>p{
    font:500 1vmax 'Lato', sans-serif;
    padding: 0.2vmax 1vmax;
    color: rgba(0, 0, 0, 0.8);  
}
.address-delivery>p>span{
    font:600 1.1vmax 'Libre Franklin', sans-serif;
    padding-right: 0.2vmax;
    letter-spacing: 1px;
}
.ordersDetails-img-container{
    
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin-top: 2vmax;
    box-shadow: rgba(93, 94, 93, 0.07) 0px 1px 2px, rgba(135, 142, 143, 0.07) 
    0px 2px 4px, rgba(137, 147, 150, 0.07) 0px 4px 8px, rgba(139, 147, 150, 0.07) 
    0px 8px 16px, rgba(139, 145, 145, 0.07) 0px 16px 32px, rgba(134, 137, 139, 0.07) 0px 32px 64px;
    padding: 1vmax;
    margin: 0.5vmax auto;
    
}
.ordersDetails-img-container>img{
    width: 7vmax;
    object-fit: contain;
    mix-blend-mode:darken;
    
}
.ordersDetails-img-container>p{
    font: 500 1vmax 'Lato', sans-serif;;
}
.orderDetails-payment-details{
    box-shadow: rgba(153, 168, 174, 0.055) 0px 2px 4px 0px, rgba(168, 173, 174, 0.205) 0px 2px 16px 0px;
    padding-bottom: 1vmax;
}
.orderDetails-payment-details>h3{
    color: rgba(78, 76, 76, 0.457);
    text-align: center;
    padding: 1vmax 0;
    border-bottom: 1px solid rgba(0, 0, 0, 0.08);
    width: 100%;
    font-size: 1.5vmax;
    text-transform: uppercase;
}
.orderDetails-price-details{
    display: flex;
    /* text-align: center; */
    justify-content: space-between;
}

.orderDetails-price-txt p{
    margin: 1.5vmax 1vmax ;
    font: 500 1.2vmax 'Roboto Slab', serif;
    color: rgba(0, 0, 0, 0.8);

}

.orderDetails-amount p{
    margin: 1.5vmax 1vmax ;
    font: 500 1.2vmax 'Roboto Slab', serif;
    color: rgba(0, 0, 0, 0.8);
}
.orderDetails-cart-total{
    display: flex;
    justify-content: space-between;
}
.orderDdetails-Total-Amount>h2{
    margin: 1vmax 1vmax ;
    font: 600 1.5vmax 'Roboto Slab', serif;
    color: rgba(0, 0, 0, 0.8);
}

.orderDetails-offer-text-cart{
    color: green;
    margin: 1.5vmax 1vmax ;
    font: 500 1vmax 'Roboto Slab', serif;
}
.order-payment{
    display: flex;
    justify-content: center;
    align-items: center;
}
.order-payment-btn{
    width:100% ;
    /* background: #fb641b; */
    background: black;
    box-shadow: 0 1px 2px 0 rgba(0,0,0,.2);
    border: none;
    color: #fff;
    padding: 1.5vmax 2vmax;
    font: 600 1.5vmax 'Libre Franklin', sans-serif;
    margin: 2vmax;
    letter-spacing: 2px;
    cursor: pointer;
    border-radius: 2px;
}
.order-payment-btn:hover{
    background: rgba(0, 0, 0, 0.587);
}
.address-text-confirm a{
    text-decoration: none;
    padding: 0.4vmax 1vmax;
    text-transform: uppercase;
    color: #2874f0;
    background-color: #d7d7e6;;
    border-radius: 2px;
    font: 500 1vmax 'Lato', sans-serif;
    margin: 1vmax;
    
}
.address-text-confirm a:hover{
color: red;
}

